import {customerList,switchCustomer} from "../../../api/user.js"
export default {
	data() {
		return {
			isCheck: true,
			mobile:"",
			customersList: [], // 用户账号列表数据
			selectIcon:'-1',
			selectid:'',
			accountsLength:0
		}
	},
	onLoad(opts) {
		this.mobile = opts.mobile;
		this.getCustomerList();
	},
	methods: {
		// 获取账号列表
		getCustomerList() {
			uni.showLoading({
				title:"加载中..."
			})
			customerList().then((res) => { 
				uni.hideLoading()
				if (res.data.code == 0) {
					
					this.customersList = res.data.result.customers
					this.accountsLength = this.customersList.length
					this.customersList.forEach((item, index) => {
						if(item.selected == 1){
							this.selectIcon = index ;
							this.selectid = item.id ;
						}
						
					})
				}
			})

		},
		submit(){ 
			uni.navigateTo({
				url: '/pages/login/login?loginFlag=1&jumpSource=1'
			})
		},
		selectCustomers(id,index){
			if(this.customersList.length <=1){
				return false;
			}
			if (index != undefined) {
				this.selectIcon = index ;
			}
			this.selectid = id;
			
		},
		defineSubmit(){
			uni.showLoading({
				title:"加载中..."
			})
			switchCustomer({
				data:{
					customer_id:this.selectid
				}
			}).then((res) => { 
				uni.hideLoading()
				if (res.data.code == 0) {
					uni.navigateBack();
				}
			})
		}
	}
}
